<template>
    <div class="user-card mainBgColor">
        <div class="card-detail">
            <div class="card-logo" @click="gotoRouter('accountInfo')">
                <user-avatars class="img" :src="userImg"></user-avatars>
            </div>
            <div class="card-info">
                <p class="shop-name" @click="gotoRouter('accountInfo')">{{ userName }}</p>
                <p class="user-level"  v-if="hasUserCard == 1" @click="gotoRouter(openUserCard ? 'userCardMobile' : '')"><i
                        class="bbx-font bbx-icon-huiyuan1"></i>
                    <span class="name-item">{{ userGroup }}166</span>
                </p>
                <p class="name" v-if="cardInfo.agentGroup">
                    <i class="bbx-font bbx-icon-mingcheng"></i>
                    <span class="name-item">{{ cardInfo.agentGroup ? cardInfo.agentGroup : ''}}</span>
                </p>
            </div>
        </div>
        <template v-if="accountType !== 2">
            <div class="card-progress" @click="gotoRouter('userCardMobile')">
            <span v-if="hasUserCard == 0">  &nbsp;  </span>
            <span v-else class="tips" >{{ $L('经验值') }}{{ nowEXP }}
                <span v-if="isAssign === 1">

                </span>
                <span v-else>
                    <i v-if="nexEXP == 0">
                        {{ $L('已达到最高级') }}
                    </i>
                    <i v-else-if="nexEXP !== 0">
                        ,{{ $L('距离下一级还差') }}{{ nexEXP }}{{ $L('经验值') }}
                    </i>

                </span>
            </span>
            <el-progress :percentage="nowEXP<=0 ? 0 : nowEXP/(nowEXP+nexEXP)*100" color="#00CDF9" :show-text="false"
                         :stroke-width="2"></el-progress>
            <p class="exp">
                <span class="span-left">EXP {{ nowEXP }}</span>
                <span  v-if="hasUserCard == 1" class="span-right">
                    <i>
                        {{ nexEXP === 0 ? '' :   $L('EXP') + Number(nowEXP+nexEXP)  }}
                    </i>
                </span>

            </p>
        </div>
        </template>
        <div class="vipCard" @click="$router.push({'name':'userCardMobile'})" v-if="openUserCard">
            <p class="p" v-if="hasUserCard">{{ $L('我的会员卡')}}</p>
            <p class="p" v-else>{{ $L('领取会员卡')}}</p>
        </div>
        <template v-if="openCart == 1">
            <div class="signIn">
                <div class="left" v-if="openSign===1" @click="doSign">
                    <span class="bbx-font bbx-icon-yuyuehuodong mainFontColor"></span>
                    <span>{{ isSign || isSignLater ? $L('今天已签到') : $L('签到领积分') }}</span>
                </div>
                <div class="left" v-else @click="$router.push({name:'accountInfo'})">
                    <span class="bbx-font bbx-icon-huahanAPP-icon-- mainFontColor"></span>
                    <span>{{ $L('我的资料') }}</span>
                </div>
                <div class="button-item" v-if="openPhone>0">
                    <div class="right" @click="bandPhone">
                        <span class="bbx-font bbx-icon-bangdingshouji mainFontColor"></span>
                        <span v-if="openPhone == 1">{{ phone!=='' ? phone : $L('绑定手机') }}</span>
                        <span v-if="openPhone == 2">{{ phone!=='' ? phone : $L('绑定手机') }}</span>
                    </div>
                </div>

                <div class="right" v-else @click="bandPhone">
                    <span class="bbx-font bbx-icon-zhanghuanquan mainFontColor"></span>
                    <span>{{ $L('安全设置') }}</span>
                </div>
                <div class="line-box"></div>
            </div>
        </template>

    </div>
</template>

<script>
    import UserAvatars from "../../common/userAvatars";
    export default {
        name: "userCard",
        components: {UserAvatars},
        props: {
            cardInfo: {
                type: Object,
                default: () => {
                    return {};
                }
            },
            accountType:{
                type: Number
            }
        },
        inject : ['reload'],
        data(){
            return {
                isSignLater: false,
            }
        },
        computed: {
            loginConfig: {
                get() {
                    return this.$store.state.loginConfig;
                }
            },
            isSign: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('isSign')) {
                        return this.loginConfig.settings.isSign;
                    } else {
                        return 0;
                    }
                }
            },
            openCart: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('openCart')) {
                        return this.loginConfig.settings.openCart;
                    } else {
                        return 0;
                    }
                }
            },
            openSign: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('openSign')) {
                        return this.loginConfig.settings.openSign;
                    } else {
                        return 0;
                    }
                }
            },
            openPhone: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('settings')
                        && this.loginConfig.settings.hasOwnProperty('openPhone')) {
                        return this.loginConfig.settings.openPhone;
                    } else {
                        return 0;
                    }
                }
            },
            phone: {
                get() {
                    if (this.loginConfig
                        && this.loginConfig.hasOwnProperty('userInfo')
                        && this.loginConfig.userInfo.hasOwnProperty('phone')) {
                        return this.loginConfig.userInfo.phone;
                    } else {
                        return null;
                    }
                }
            },
            userImg: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('userImg')) {
                        return this.cardInfo.userImg;
                    } else {
                        return '';
                    }
                }
            },
            userName: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('userName')) {
                        return this.cardInfo.userName;
                    } else {
                        return this.$L('您好！');
                    }
                }
            },
            openUserCard: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('openUserCard') && this.cardInfo.openUserCard === 1) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            hasUserCard:{
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('hasUserCard') && this.cardInfo.hasUserCard === 1) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            isAssign : {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('isAssign')) {
                        return true;
                    } else {
                        return false;
                    }
                }
            },
            userGroup: {
                get() {
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('userGroup')) {
                        return this.cardInfo.userGroup;
                    } else {
                        return '';
                    }
                }
            },
            cardDesc: {
                get() {
                    return this.$L('经验值{$1}，距离下一级还差{$2}经验值', [this.nowEXP, this.nexEXP])
                }
            },
            nowEXP: {
                get() {
                    let nowEXP = 0;
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('nowEXP')) {
                        nowEXP = this.cardInfo.nowEXP;
                    }
                    return nowEXP;
                }
            },
            nexEXP: {
                get() {
                    let nextEXP = 0;
                    if (this.cardInfo && this.cardInfo.hasOwnProperty('nexEXP')) {
                        nextEXP = this.cardInfo.nexEXP;
                    }
                    return nextEXP;
                }
            }
        },
        methods: {
            gotoRouter(name) {
                this.$router.push({name: name});
            },
            doSign() {
                const _self = this;
                _self.$api("doSign", {}, function (reData) {
                    if (reData && reData.flag === 1) {
                        _self.$message.success(_self.$L('签到成功！获得{$1}积分', [reData.data.score]));
                        _self.reload();
                        _self.isSignLater = true;
                    } else if (reData && reData.flag === 2) {
                        _self.$message.success(_self.$L('今天已签到！'));
                        _self.isSignLater = true;
                    } else {
                        _self.$message.error(_self.$L('签到失败！请稍后重试'));
                    }
                }, function (error) {
                    console.log(error);
                    _self.$message.error(_self.$L('签到失败！请稍后重试'));
                });
            },
            bandPhone() {
                if (this.openPhone === 2) {
                    this.$router.push({name: 'accountInfo'});
                } else {
                    this.$router.push({name: 'accountSafety'});
                }

            },
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib";
    .user-card{
        position: relative;
        width: 100%;
        height: 265px;
        padding: 30px 20px 0;
        color: #fff;
        @extend .no-select;

        .card-detail {
            height: 100px;
            @extend .no-select;

            .card-logo {
                display: inline-block;
                vertical-align: top;
                width: 100px;
                height: 100px;
                line-height: 100px;
                box-sizing: border-box;
                border-radius: 50%;
                padding: 10px;
                border: 1px solid #fff;
                color: #ffffff;
                text-align: center;
                cursor: pointer;

                .img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    line-height: inherit;

                    /deep/ .img-error {
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                    }
                    /deep/ .el-image__inner{
                        border-radius: 50%;
                    }
                }

                &:active {
                    opacity: 0.8;
                }
            }
            .card-info{
                display: inline-block;
                vertical-align: top;
                height: 100px;
                width: calc(100% - 100px);
                padding: 5px 15px;
                .shop-name{
                    font-size: 20px;
                    line-height: 35px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    width: 110px
                }
                .bbx-font{
                    font-size: 20px;
                }
                .user-level{
                    height: 30px;
                    line-height: 30px;
                    .bbx-icon-huiyuan1{
                        color: #FCD23D;
                        margin-right: 5px;
                        float: left;
                    }
                }
                .name{
                    height: 30px;
                    line-height: 30px;
                    .bbx-font{
                        float: left;
                        margin-right: 5px;
                    }
                }
                .name-item{
                    float: left;

                }
            }
        }

        .card-progress {
            margin-top: 20px;
            cursor: pointer;
            .tips{
                width: 100%;
                display: inline-block;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            &:active{
                opacity: 0.8;
            }

            .el-progress {
                margin: 5px 0;
            }

            .exp {
                .span-left {
                    float: left;
                }

                .span-right {
                    float: right;
                }
            }
            .num-name{
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }

        .vipCard {
            background: url("../../../assets/img/home/vipCard.png") no-repeat left bottom;
            width: 133px;
            height: 117px;
            position: absolute;
            top: -15px;
            right: -15px;
            cursor: pointer;

            .p {
                color: #FF6000;
                position: absolute;
                top: 60px;
                left: 40px;
                font-weight: 700;
                transform: rotate(38deg);
                -ms-transform: rotate(38deg);
                -moz-transform: rotate(38deg);
                -webkit-transform: rotate(38deg);
                -o-transform: rotate(38deg);
            }

            &:active {
                top: 0;
                right: 0;
            }
        }

        .signIn{
            width: calc(100% - 40px);
            height: 70px;
            position: absolute;
            bottom: -15px;
            left: 20px;
            background-color: #fff;
            z-index: 99;
            box-shadow: 0 2px 5px rgba(0,0,0,0.1);
            line-height: 70px;
            text-align: center;
            color: #666;
            .left{
                position: absolute;
                width: 50%;
                height: 100%;
                left: 0;
                top: 0;
                &:active{
                    opacity: 0.8;
                }
            }
            .right{
                position: absolute;
                width: 50%;
                height: 100%;
                left: 50%;
                top: 0;
                .modify{
                    padding: 4px 10px;
                    border-radius: 20px;
                    margin-left: 10px;
                    color: #fff;
                }
                &:active{
                    opacity: 0.8;
                }
            }
            .bbx-font{
                font-size: 20px;
                vertical-align: middle;
                margin-right: 10px;
            }

            .line-box{
                position: absolute;
                height: calc(100% - 30px);
                width: 1px;
                top: 15px;
                left: 50%;
                transform: translateX(-50%);
                background-color: #F2F6FC;
            }
        }
    }
</style>
