<template>
    <div class="user-detail">
        <div class="data-items" v-for="item in dataArr">
            <div class="items-box" @click="gotoRouter(item)">
                <div class="icon-box">
                    <i class="icon mainFontColor" v-bind:class="item.icon"></i>
                </div>
                <div class="info">
                    <div class="title">
                        <div class="txt">{{item.title}}</div>
                    </div>
                    <div class="value">
                        <div class="txt">{{item.value}}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "userDetail",
        props: {
            dataArr: {
                type: Array,
                default: () => {
                    return [];
                }
            }
        },
        methods:{
            //点击
            gotoRouter(item) {
                if (item && item.hasOwnProperty('router')) {
                    let params = {};
                    if (item.hasOwnProperty('params')) {
                        Object.assign(params, item.params);
                    }
                    if (item.hasOwnProperty('paramsData')) {
                        Object.assign(params, item.paramsData);
                    }
                    this.$router.push({name: item.router, params: params});
                } else if (item && typeof item.func === "function") {
                    if (item.hasOwnProperty('funcData')) {
                        item.func(item.funcData);
                    } else {
                        item.func();
                    }
                }
            }
        }
    }

</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib";
    .user-detail{
        width: 100%;
        overflow: hidden;
        background: #fff;
        padding-top: 15px;
        @extend .no-select;
        .data-items{
            width: calc(100% / 3);
            height: 85px;
            line-height: 85px;
            float: left;
            text-align: center;
            position: relative;
            border-bottom: 1px solid #F2F6FC;
            border-right: 1px solid #F2F6FC;
            &:active{
                box-shadow: 0 0 10px #ccc;
            }
            &:nth-child(3),&:nth-child(6){
                border-right:0;
            }
            &:nth-child(4),&:nth-child(5),&:nth-child(6){
                border-bottom:0;
            }

            &:nth-child(3n){
                margin-right: 0;
            }

            .icon-box {
                position: absolute;
                width: 45%;
                height: 100%;
                top: 0;
                left: 0;
                z-index: 100;
                text-align: right;
                padding-right: 10px;

                .icon {
                    font-size: 26px;
                }
            }

            .info {
                position: absolute;
                width: calc(65% - 10px);
                height: 100%;
                top: 0;
                left: 45%;
                z-index: 90;
                text-align: left;

                .title {
                    position: relative;
                    width: 100%;
                    height: 50%;
                    font-size: 12px;
                    color: #999;

                    .txt {
                        position: absolute;
                        line-height: 100%;
                        bottom: 1px;
                        left: 0;
                        width: inherit;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                }

                .value {
                    position: relative;
                    width: 100%;
                    height: 50%;
                    font-size: 12px;
                    color: #666;

                    .txt {
                        position: absolute;
                        line-height: 100%;
                        top: 1px;
                        left: 0;
                        width: inherit;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                    }
                }
            }
        }
    }
</style>