<template>
    <div class="home" v-loading="loading">
        <userCard :account-type="baseInfo && baseInfo.hasOwnProperty('accountInfo') ? baseInfo.accountInfo.accountType:''" :card-info="baseInfo && baseInfo.hasOwnProperty('userInfo') ? baseInfo.userInfo : {}" v-if="!loading"></userCard>
        <template v-if="baseInfo.hasOwnProperty('accountInfo') && baseInfo.accountInfo.hasOwnProperty('accountType') && baseInfo.accountInfo.accountType !== 2">
            <user-detail :data-arr="dataArr" v-if="!loading"></user-detail>
        </template>
        <menuGroup></menuGroup>
    </div>
</template>

<script>
    import userCard from "../../components/mobile/home/userCard.vue"
    import userDetail from "../../components/mobile/home/userDetail.vue"
    import menuGroup from "../../components/mobile/home/menuGroup.vue"
    export default {
        name: "home",
        components: {
            userCard,userDetail,menuGroup
        },
        data() {
            return {
                loading: true,
                baseInfo: {},
            }
        },
        computed: {
            dataArr: {
                get() {
                    let re = [];
                    if (this.baseInfo.hasOwnProperty('accountInfo')) {
                        if (this.baseInfo.accountInfo.accountType === 2){
                            //展示版
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weiduxinxi',
                                    title: this.$L('未读信息'),
                                    value: this.baseInfo.accountInfo.message,
                                    router: 'messageList',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-liuyanban',
                                    title: this.$L('我的留言'),
                                    value: this.baseInfo.accountInfo.leaveCount,
                                    router: 'leaveList',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-xinxifabu',
                                    title: this.$L('信息发布'),
                                    value: this.baseInfo.accountInfo.informationCount,
                                    router: 'information',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('address')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-shouhuodizhi',
                                    title: this.$L('我的地址'),
                                    value: this.baseInfo.accountInfo.address,
                                    router: 'addressList'
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weibiaoti-2',
                                    title: this.$L('账户管理'),
                                    value: '',
                                    router: 'accountSafety',
                                });
                            }
                        } else {
                            if (this.baseInfo.accountInfo.hasOwnProperty('balance')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weibiaoti--29',
                                    title: this.$L('余额'),
                                    value: this.baseInfo.accountInfo.balance,
                                    router: 'payRecord',
                                    link: {
                                        text: this.$L('充值'),
                                        router: 'payCharge',
                                    }
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('integral')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-wodejifen',
                                    title: this.$L('积分'),
                                    value: this.baseInfo.accountInfo.integral,
                                    router: 'pointList'
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('coupon')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-wodeqiaquan',
                                    title: this.$L('优惠券'),
                                    value: this.baseInfo.accountInfo.coupon,
                                    router: 'couponList',
                                    params:{
                                        type:'canUse'
                                    }
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('message')) {
                                re.push({
                                    icon: 'bbx-font bbx-icon-weiduxinxi',
                                    title: this.$L('未读信息'),
                                    value: this.baseInfo.accountInfo.message,
                                    router: 'messageList',
                                });
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('accountType') && this.baseInfo.accountInfo.accountType === 4) {
                                if (this.baseInfo.accountInfo.hasOwnProperty('reservationOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-wodeyuyue',
                                        title: this.$L('预约订单'),
                                        value: this.baseInfo.accountInfo.reservationOrder,
                                        router: 'reservation',
                                        params: {
                                            type: 'all'
                                        }
                                    });
                                }
                            } else {
                                if (this.baseInfo.accountInfo.hasOwnProperty('deliveryOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-daishouhuo',
                                        title: this.$L('待收货'),
                                        value: this.baseInfo.accountInfo.deliveryOrder,
                                        router: 'orderList',
                                        params: {
                                            type: 'needReceipt'
                                        }
                                    });
                                }
                            }
                            if (this.baseInfo.accountInfo.hasOwnProperty('accountType') && this.baseInfo.accountInfo.accountType === 4) {
                                if (this.baseInfo.accountInfo.hasOwnProperty('reservationOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-wodeyuyue',
                                        title: this.$L('预约待付款'),
                                        value: this.baseInfo.accountInfo.paymentOrder,
                                        router: 'reservation',
                                        params: {
                                            type: 'payment'
                                        }
                                    });
                                }
                            } else {
                                if (this.baseInfo.accountInfo.hasOwnProperty('paymentOrder')) {
                                    re.push({
                                        icon: 'bbx-font bbx-icon-daifukuan',
                                        title: this.$L('待付款'),
                                        value: this.baseInfo.accountInfo.paymentOrder,
                                        router: 'orderList',
                                        params: {
                                            type: 'needPay'
                                        }
                                    });
                                }
                            }
                        }
                    }
                    return re;
                }
            },
        },
        created() {
            this.getData();
        },
        methods: {
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('indexBaseInfo', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.baseInfo = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';
    .home{
        position: relative;
        background-color: #EFF1F5;
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>